.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 100px 20px;
  display: flex;
  flex-direction: column;
}

.title {
  margin-bottom: 12px;
  text-align: center;
}

.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  list-style-type: none;
  margin-bottom: 20px;
}
.list a {
  color: #2e2f42;
  font-size: 20px;
  font-weight: 500;
}
